import { Alert } from 'selenium-webdriver';
import { Cidade } from './../../shared/models/cidade';
import { EstadoBr } from './../../shared/models/estado-br.model';
import { DropdownService } from './../../shared/services/dropdown.service';
import { ConsultaCepService } from './../../shared/services/consulta-cep.service';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { cepValidator, getLabelAllFields, getFormValidationErrors } from '../../shared/form-validation';
import { map, tap, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { empty } from 'rxjs';
import { EstudanteService } from '../services/estudante.service';

@Component({
    selector: 'app-modal-andamento',
    templateUrl: 'modal-andamento.html',
    styleUrls: ['modal-andamento.css']
})
export class ModalAndamento implements OnInit {
    @Input() public formPai: any;
    @Output() passEntry: EventEmitter<any> = new EventEmitter();

    form: FormGroup;
    submitted = false;
    alerts = [];
    carregando = false;
    ocultarFormulario = true;
    desabilitarBotaoSalvar = false;

        listaSMS: any = [{ codigo: 'Sim', descricao: 'Sim' }, { codigo: 'Não', descricao: 'Não' }];
    listaStatus: any = [{ codigo: '0', descricao: 'Em análise' }, { codigo: '1', descricao: 'Aguardando parecer' }, { codigo: '2', descricao: 'Concluído' }, { codigo: '3', descricao: 'Cancelado' }];

    constructor(
        public activeModal: NgbActiveModal,
        private fb: FormBuilder,
        private service: EstudanteService,
        private cepService: ConsultaCepService,
        private dropdownService: DropdownService,

    ) { }

    ngOnInit() {

        this.form = this.fb.group({
            id_pai: [null],
            id: [null ],
            data: [null, [this.validarData]],
            status: [null, [Validators.required]],
            sms: [null],
            descricao: [null],
            cpf: [null],            
            nome: [null],
            email: [null],
            celular: [null],


        })

        this.form.get('id_pai').setValue(this.formPai.id);        
        this.form.get('cpf').setValue(this.formPai.cpf);
        this.form.get('nome').setValue(this.formPai.nome);
        this.form.get('email').setValue(this.formPai.email);
        this.form.get('celular').setValue(this.formPai.celualar);
        

    }

   novo(){
       this.ocultarFormulario = false;
       var data = new Date(),
        dia  = data.getDate().toString(),
        diaF = (dia.length == 1) ? '0'+dia : dia,
        mes  = (data.getMonth()+1).toString(), //+1 pois no getMonth Janeiro começa com zero.
        mesF = (mes.length == 1) ? '0'+mes : mes,
        anoF = data.getFullYear();
       this.form.get('data').setValue(diaF+"/"+mesF+"/"+anoF);       
   }

   cancelar(){
    this.ocultarFormulario = true;
    }

    sair(){
        this.ocultarFormulario = true;
    }

    excluir(andamento){
        if (confirm('Confirma a exclusão deste andamento ?')){
            this.service.excluirAndamento(andamento)
            .subscribe(
              response => {
                console.log("Status" + response.status);          
                if (response.status.codigo == 0){
                  this.alerts = Array.from([{ type: 'success', message: response.status.mensagem }])
                  this.formPai.andamentos = Array.from(response.dados);
                  this.ocultarFormulario = true;                
                }else{
                  this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
                  this.desabilitarBotaoSalvar = false;          
                }
              this.carregando = false;
              },
              err => {
                this.carregando = false;
                this.alerts = Array.from([{ type: 'danger', message: err.message }])          
                console.log(err);
              });
  
        }

    }
    

    close(alert: Alert) {
        this.alerts.splice(this.alerts.indexOf(alert), 1);
    }

    getLabelField(fieldName: string) {
        return getLabelAllFields(fieldName);
    }

    validarData(control: AbstractControl): { [key: string]: any } | null {
        const v = control.value;
        if (null != v && v.length == 8) {
            const data = v.substr(0, 2) + "/" + v.substr(2, 2) + "/" + v.substr(4, 4);
            const patternData = /^(((0[1-9]|[12][0-9]|3[01])([-.\/])(0[13578]|10|12)([-.\/])(\d{4}))|(([0][1-9]|[12][0-9]|30)([-.\/])(0[469]|11)([-.\/])(\d{4}))|((0[1-9]|1[0-9]|2[0-8])([-.\/])(02)([-.\/])(\d{4}))|((29)(\.|-|\/)(02)([-.\/])([02468][048]00))|((29)([-.\/])(02)([-.\/])([13579][26]00))|((29)([-.\/])(02)([-.\/])([0-9][0-9][0][48]))|((29)([-.\/])(02)([-.\/])([0-9][0-9][2468][048]))|((29)([-.\/])(02)([-.\/])([0-9][0-9][13579][26])))$/;
            if (!patternData.test(data)) {
                return { dataInvalida: true };
            }
        }
    }
    

    salvar() {
        console.log('Salvando Andamento');
        this.alerts = [];
        this.submitted = true;
        this.desabilitarBotaoSalvar = true;
    
        // stop here if form is invalid
        if (this.form.invalid) {
   
          this.alerts = getFormValidationErrors(this.form.controls);
    
          console.log('Formulário inválido');
          this.desabilitarBotaoSalvar = false;          
          return;
        }
        this.carregando = true;

        this.service.salvarAndamento(this.form)
          .subscribe(
            response => {
              console.log("Status" + response.status);          
              if (response.status.codigo == 0){
                this.alerts = Array.from([{ type: 'success', message: response.status.mensagem }])
                this.formPai.andamentos = Array.from(response.dados);
                this.ocultarFormulario = true;                
              }else{
                this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
                this.desabilitarBotaoSalvar = false;          
              }
            this.carregando = false;
            },
            err => {
              this.carregando = false;
              this.alerts = Array.from([{ type: 'danger', message: err.message }])          
              console.log(err);
            });
    }
}