import { ModalEstudante } from './modal-estudante/modal-estudante.components';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { environment } from '../../environments/environment';
import { Alert } from 'selenium-webdriver';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { EstudanteService } from './services/estudante.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Status } from './model/Pesquisa';
import { ModalAndamento } from './modal-andamento/modal-andamento.components';

@Component({
  selector: 'app-estudante',
  templateUrl: './estudante.component.html',
  styleUrls: ['./estudante.component.css']
})
export class EstudanteComponent implements OnInit {

  lista: [] = [];
 
  page = 0;
  pageSize = 50;
  collectionSize = 0;
  total = 0;
  carregando = false;
  url_base = `${environment.API}`;
  alerts: any;
  subscription = null;
  listaStatus = [{ codigo: "0", descricao: "Incluida pelo Site" }, { codigo: "1", descricao: "Documentação Pendente" }, { codigo: "2", descricao: "Aguardando impressão" },  { codigo: "9", descricao: "Concluida" }, { codigo: "6", descricao: "Cancelada" }];
  status: Status;
  queryBusca: String;

  constructor(
    private router: Router,
    public service: EstudanteService,
    private modalService: NgbModal
  ) { }

  ngOnInit() {

    this.status = this.listaStatus[0];
        
    this.carregando = true;

    this.subscription = this.service.getListaByStatus("0")
      .subscribe(
        response => {
          console.log("Status" + response.status);
          if (response.status.codigo == 0) {
            this.lista = response.dados;
          } else {
            this.lista = [];
            this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
          }
          this.carregando = false;
        },
        err => {
          this.carregando = false;
          this.alerts = Array.from([{ type: 'danger', message: err.message }])
          console.log(err);
        });



  }

  novo() {
    this.router.navigate(['/estudante', 'novo']);
  }

  openModal(estudante) {

    this.subscription = this.service.get(estudante.cpf)
      .subscribe(
        response => {
          console.log("Deu certo: " + response.dados);
          var currentObject = response.dados[0];
          const modalRef = this.modalService.open(ModalEstudante, { size: 'lg' });
          console.log('Antes');
          modalRef.componentInstance.estudante = response.dados[0];
          //            modalRef.componentInstance.loadDados(registro);
        });

    console.log('Depois');

  }

  openModalAndamento(formPai) {

    this.subscription = this.service.getAndamentos(formPai.id)
    .subscribe(
      response => {

        var currentObject = response.dados[0];
        const modalRef = this.modalService.open(ModalAndamento, { size: 'lg' });
        formPai.andamentos = Array.from(response.dados);
        modalRef.componentInstance.formPai = formPai;
      });




}


  pesquisar() {
    this.alerts = [];
    this.carregando = true;
    this.subscription = this.service.getBusca(this.queryBusca)
      .subscribe(
        response => {
          console.log("Status" + response.status);
          if (response.status.codigo == 0) {
            this.lista = response.dados;
          } else {
            this.lista = [];
            this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
          }
          this.carregando = false;
        },
        err => {
          this.carregando = false;
          this.alerts = Array.from([{ type: 'danger', message: err.message }])
          console.log(err);
        });

    this.collectionSize = this.lista.length;

  }

  filtroStatus(){

    const elem: HTMLInputElement =<HTMLInputElement>document.getElementById('comboFiltro');
    this.subscription = this.service.getListaByStatus(elem.value)
    .subscribe(
      response => {
        console.log("Status" + response.status);
        if (response.status.codigo == 0) {
          this.lista = response.dados;
        } else {
          this.lista = [];
          this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
        }
        this.carregando = false;
      },
      err => {
        this.carregando = false;
        this.alerts = Array.from([{ type: 'danger', message: err.message }])
        console.log(err);
      });



    this
  }

  close(alert: Alert) {
    this.alerts.splice(this.alerts.indexOf(alert), 1);
  }

  ngOnDestroy() {
    if (this.subscription != null) {
      this.subscription.unsubscribe();
    }
  }

 
}