import { ModalEstudante } from './modal-estudante/modal-estudante.components';
import { RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { EstudanteComponent } from './estudante.component';
import { EstudanteGuard } from '../guards/estudante.guard';

const estudanteRoutes = [
    {path: '', component: EstudanteComponent, 
     canActivateChild: [EstudanteGuard],
     children : [
        {path: 'novo', component: ModalEstudante}
    ]}
];
@NgModule({
    imports: [RouterModule.forChild(estudanteRoutes)],
    exports: [RouterModule]
})
export class EstudanteRoutingModule {}

